export default {
    data(){
      return{
        value1: 0,
        value2: 'Login.vue',
        checkstyle:[
          { text: '写字楼出租', value: 1 },
          { text: '写字楼出售', value: 2 },],
        option1: [
          {text:"区域",value:null},
          { text: '渝中区', value: '渝中区' },
          { text: '江北区', value: "江北区"},
          { text: '南岸区', value: "南岸区" },
          { text: '九龙坡区', value: '九龙坡区' },
          { text: '沙坪坝区', value:"沙坪坝区" },
          { text: '大渡口区', value: "大渡口区" },
          { text: '北碚区', value: '北碚区' },
          { text: '渝北区', value: "渝北区"},
          { text: '巴南区', value: "巴南区" },
          { text: '两江新区', value: "两江新区" }
        ],
        option2: [
          { text: '面积', value: null },
          {
            value: null,
            text: '不限'
          },{
            value: '0-100',
            text: '100m²以下'
          }, {
            value: '100-200',
            text: '100-200m²'
          }, {
            value: '200-300',
            text: '200-300m²'
          }, {
            value: '300-500',
            text: '300-500m²'
          }, {
            value: '500-800',
            text: '500-800m²'
          }, {
            value: '800-1500',
            text: '800-1500m²'
          }, {
            value: '1500',
            text: '1500以上'
          }
        ],
        option3: [
          { text: '价格', value: null },
          {
            value: '',
            text: '不限'
          },{
            value: '0-50',
            text: '50元/m²·月以下'
          }, {
            value: '50-80',
            text: '50-80元/m²·月'
          }, {
            value: '80-100',
            text: '80-100元/m²·月'
          }, {
            value: '100',
            text: '100元以上'
          }
        ],
        option4: [
          { text: '高级搜索', value: 'Login.vue' },
          { text: '好评排序', value: 'b' },
          { text: '销量排序', value: 'c' },
        ],
        parms:{
          name:null,
          region:null,
          price:null,
          area:null,
          pageNo:1,
          rors:1
        },
        users:[],
        loading: false,   //是否处于加载状态
        finished: false,  //是否已加载完所有数据
        isLoading: false,   //是否处于下拉刷新状态
        totalpage:null
      }
    },
  created() {
      this.getmsg()
  },
  mounted(){
    let winHeight = document.documentElement.clientHeight                          //视口大小
    document.getElementById('list-content').style.height = (winHeight - 80) +'px'  //调整上拉加载框高度
  },
  methods:{
    changeoption(){
      this.users=[]
      this.parms.pageNo=1
      this.getmsg()
    },
    onLoad() {      //上拉加载
      setTimeout(() => {
        this.loading = false;
        if (this.parms.pageNo<this.totalpage){
          this.parms.pageNo++
          this.getmsg()
        } else {
          this.finished = true;
          this.$toast('已经加载完毕!');
        }
        //
      }, 500);
    },
    onRefresh() {       //下拉刷新
      setTimeout(() => {
        this.finished = false;
        this.isLoading = false;
        this.parms.pageNo=1
        this.users=[]
        this.getmsg()
      }, 500);
    },
    getmsg(){
      let formdata;
      formdata=new FormData()
      for (let k in this.parms){
        if(this.parms[k]){
          formdata.append(k,this.parms[k])
        }
      }
      this.$axios.post("/house/list",formdata).then((res) => {
        if (res.data.code=="error"){
          this.$toast.fail(res.data.message);
        } else {
          this.totalpage=res.data.data.totalpage
          res.data.data.rows.forEach((item)=>{
            this.users.push(item)
          })
          console.log(this.users)
          this.users.forEach((item,index)=>{
            this.users[index].houseimg=item.houseimg.split(",")
          })
          console.log(this.users)
        }
      })
    },
    checkitem(item){
      console.log(item)
        this.$router.push({
          path:"/details",
          query:{
            id:item.pk_house
          }
        })
    }
  }
}



